<template>
	<div class="lhj">
		<div class="header">
			<img src="../../assets/images/aa.png">
		</div>
		<div class="bg">
			<div class="top">
				<ul>
					<li v-for="item in img">
						<img :src="item.imgurl">
					</li>
				</ul>
			</div>
			<div class="mid">
				<ul>
					<li v-for="item in img">
						<img :src="item.imgurl">
					</li>
				</ul>
			</div>
			<div class="bot">
				<ul>
					<li v-for="item in img">
						<img :src="item.imgurl">
					</li>
				</ul>
			</div>
			
		</div>
		<div class="bottom">
			<img src="../../assets/images/bb.png">
		</div>

		<div class="footer">
			<button id="a">开始</button>
			<button id="b">结束</button>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				img:[
					{
						imgurl: require("../../assets/images/a.png")
					},
					{
						imgurl: require("../../assets/images/b.png")
					},
					{
						imgurl: require("../../assets/images/c.png")
					},
					{
						imgurl: require("../../assets/images/d.png")
					},
					{
						imgurl: require("../../assets/images/a.png")
					},
				]
			}
		},
		mounted(){

			let top = document.querySelector('.top');
			let mid = document.querySelector('.mid');
			let bot = document.querySelector('.bot');

			let topul = top.querySelector('ul');
			let midul = mid.querySelector('ul');
			let botul = bot.querySelector('ul');

			let img = document.querySelector('li');

			let a = document.getElementById('a');
			let b = document.getElementById('b');
			
			let aindex = 0;
			let bindex = 0;
			let cindex = 0;

			let timertop = null;
			let timermid = null;
			let timerbot = null;



			function topmove(offset) {
				topul.style.transition = '.5s';
				topul.style.top = -parseInt(offset) * aindex + 'px';
			}

			function move(offset) {
				midul.style.transition = '.5s';
				midul.style.top = -parseInt(offset) * bindex + 'px';
			}

			function botmove(offset) {
				botul.style.transition = '.5s';
				botul.style.top = -parseInt(offset) * cindex + 'px';
			}

			function topset(setul) {
				if (aindex == 4) {
					aindex = 0;
					setul.style.transition = '0s';
					setul.style.top = '0px';
				}
				aindex ++;
			}
			function midset(setul) {
				if (bindex == 4) {
					bindex = 0;
					setul.style.transition = '0s';
					setul.style.top = '0px';
				}
				bindex ++;
			}
			function botset(setul) {
				if (cindex == 4) {
					cindex = 0;
					setul.style.transition = '0s';
					setul.style.top = '0px';
				}
				cindex ++;
			}
				
			function animate(){
				timertop = setInterval(() =>{
					topset(topul);
					topmove(img.offsetHeight);
				},175);
				timermid = setInterval(() =>{
					midset(midul);
					move(img.offsetHeight);
				},125);
				timerbot = setInterval(() =>{
					botset(botul);
					botmove(img.offsetHeight);
				},150);
				
			}

			a.onclick = function(){
				animate()
			} 

			b.onclick =function(){
				setTimeout(()=>{
					clearInterval(timertop);
				},1000)
				setTimeout(()=>{
					clearInterval(timermid)
				},1200)
				setTimeout(()=>{
					clearInterval(timerbot)
				},1400)
				if (aindex == bindex && bindex == cindex) {
					alert('you win')
				}
			}
			
		}
	}
</script>

<style lang="scss" scoped>
	.lhj{
		.header, .bottom{
			img{
				width: 100%;
				height: auto;
			}
		}
		.bg{
			height: 2rem;
			display: flex;
			background:black;
			justify-content: center;
			div{
				width: 25%;
				height: 2rem;
				background: white;
				margin: 3%;
				text-align: center;
				overflow: hidden;
				position: relative;
				ul{
					position:absolute;
					top: 0;
					transition: 1s;
					li{
						width: 100%;
						height: 2rem;
						img{
							width: 80%;
							height: auto;
							margin-top: .2rem;
							vertical-align:middle

						}
					}
				}
				
			}			
		}

		.footer{
			display: flex;
			justify-content: center;
			button{
				margin: .6rem .6rem;
				width: 1rem;
				height: 1rem;
				background:red;
				text-align: center;
				color:white;
				border-radius: 50%;
			}
		}
		
	}
</style>
